<template>
  <div class="scholarlyMeeting">
    <div class="banner">
        <div class="container">
          <img src="../assets/img/newsBg.jpg" alt="">
        </div>
    </div>
    <div class="meetingContainer">
        <div class="container">
          <bread :data="[{name:'首页',url:'/'},{name:'学术交流',url:''},{name:activeItem.name,url:''}]"></bread>
          <div class="menuList">
            <p class="title">栏目导航</p>
            <ul>
              <li v-for="(item,index) in menuList" :key="index" @click="changeMenu(item)">{{item.name}}</li>
            </ul>
          </div>
          <div class="newsList">
            <ul>
              <li  v-for="(item,index) in newsList" :key="index">
                <p>
                  <span class="icon"><i class="el-icon-document"></i></span>
                  <span class="title"><router-link to="NewsDetail">{{item.title}}</router-link></span>
                  <span class="time">{{item.time}}</span>
                </p>
              </li>
            </ul>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
import bread from './compoments/bread.vue'
export default {
  components: { bread },
  data(){
    return{
      activeItem:{name:'行业资讯'},
      menuList:[{name:'行业资讯'},{name:'技术交流'},{name:'创业大赛'}],
      newsList:[
        {
          title:"在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析",
          time:'2020.11.03'
        },
        {
          title:"在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析",
          time:'2020.11.03'
        },
        {
          title:"在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析",
          time:'2020.11.03'
        },
        {
          title:"在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析",
          time:'2020.11.03'
        },
        {
          title:"在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析",
          time:'2020.11.03'
        },
        {
          title:"在政策推动和市场导向下选用最佳的垃圾焚烧炉的分析",
          time:'2020.11.03'
        }
      ]
    }
  },
  methods: {
    changeMenu(item){/*  */
      this.activeItem.name = item.name
    }
  },
}
</script>
<style lang="less">
.scholarlyMeeting{
  .banner{
    img{
      height: 273px;
      width: 100%;
    }
  }
  .meetingContainer{
    .bread{
      margin: 10px 0;
      text-align: left;
    }
    .menuList{
      display:  inline-block;;
      width: 293px;
      min-height:540px;
      border:1px solid #10bbb8;
      margin-bottom: 10px;
      padding: 20px;
      text-align: left;
      float: left;
      .title{
        font-weight: 400;
        font-style: normal;
        font-size: 25px;
        color: #555555;
        padding-bottom: 10px;
        border-bottom: 2px solid #028cd6;
      }
      ul{
        li{
          font-weight: 400;
          font-style: normal;
          color: #555555;
          padding: 10px 0;
          border-bottom: 1px dashed #10bbb8;
          &:hover{
            cursor: pointer;
            color: #10bbb8;
          }
        }
      }
    }
    .newsList{
      display: inline-block;
      width: 809px;
      padding: 20px;
      min-height: 540px;
      padding: 20px;
      border:1px solid #10bbb8;
      float: right;
      ul{
        overflow: hidden;
        li{
          overflow: hidden;
          border-bottom: 1px dashed #555;
          padding: 10px 0 10px 0;
          .icon{
            display: inline-block;
            float: left;
            height: 16px;
            width: 16px;
            line-height: 16px;
            margin: 0 10px;
          }
          .title{
            float: left;
            line-height: 16px;
            height: 16px;
            a{
              display: inline-block;
              color: #555;
              text-overflow:ellipsis ;
              max-width: 680px;
              height: 16px;
              overflow: hidden;
              word-break: break-all;
              white-space: nowrap;
              &:hover{
                color: #028cd6;
              }
            }
          }
          .time{
            line-height: 16px;
            display: inline-block;
            float: right;
          }
        }
      }
    }
  }
}
</style>


